import React from "react";
import {connect} from "react-redux";
import OraSwitcherSelect from "eshop/modules/core/components/ui/OraSwitcherSelect"
import {getMarketContext} from "eshop/modules/configurator/selectors/filters";
import {isCartMobile} from "eshop/modules/cart/selectors";
class ProductSwitcherHeaderView extends React.PureComponent {
    onChange(e){
        this.props.onChange(e.value)
    }

    render() {
        if (this.props.isB2B && !this.props.isCartMobile && this.props.channels.sales === "WWW") {
            return null;
        }
        return (
            <div className="l-full-row g-gray1-bg">
                <div className="l-page-row u-padding-l">
                    <div className="l-row ">
                        <div className="l-col l-col-12  u-padding-top-s u-padding-m">
                            <OraSwitcherSelect
                                options={this.props.options}
                                id="offerTypeFilter"
                                selected={this.props.selectedOfferType}
                                onChange={this.onChange.bind(this)}
                                className=" u-small-left u-left u-w-100 u-small-w-auto u-medium-w-100 u-small-w-100"
                                selectClassName="opl-input--size-m"
                                singleOptionClassName="u-small-no-padding-left"
                            />
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
const mapDispatchToProps = dispatch => ({

});

const mapStateToProps = state => ({
    isCartMobile: isCartMobile(state),
    isB2B: getMarketContext(state) === "B2B",
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductSwitcherHeaderView);
